<template>
	<div class="page">
		<el-container class="page">
			<el-aside style="width: auto;">
				<el-menu class="page" :default-active="activeIndex" background-color="rgb(45, 52, 54)" text-color="#fff" collapse router @select="handleSelect">
					<el-menu-item index="/home/education">
						<i class="el-icon-reading"></i>
						<span slot="title">教育背景</span>
					</el-menu-item>
					<el-menu-item index="/home/skill">
						<i class="el-icon-collection"></i>
						<span slot="title">技术知识</span>
					</el-menu-item>
					<el-menu-item index="/home/work">
						<i class="el-icon-monitor"></i>
						<span slot="title">工作经历</span>
					</el-menu-item>
					<el-menu-item index="/home/project">
						<i class="el-icon-tickets"></i>
						<span slot="title">项目展示</span>
					</el-menu-item>
					<el-menu-item index="/home/about">
						<i class="el-icon-user"></i>
						<span slot="title">关于我</span>
					</el-menu-item>
				</el-menu>
			</el-aside>
			<el-main class="d-flex flex-column p-0 position-relative">
				<LPANGFEI class="position-absolute" />
				<textra class="position-fixed bottom-0 mb-2 font-big text-white textra" :data="words" :timer="1.5" infinite filter="top-bottom" />
				<router-view />
			</el-main>
		</el-container>
	</div>
</template>

<script>
import { mapState, mapMutations } from 'vuex';
import { SET_ACTIVE_INDEX } from '@/store/mutation-types.js';
import LPANGFEI from '@/components/LPANGFEI.vue';
export default {
	name: 'Home',
	data: () => ({
		words: ['春有百花秋有月', '夏有凉风冬有雪', '若无闲事挂心头', '便是人间好时节']
	}),
	components: {
		LPANGFEI
	},
	computed: {
		...mapState(['activeIndex'])
	},
	methods: {
		...mapMutations({
			SET_ACTIVE_INDEX
		}),

		handleSelect(index) {
			this.SET_ACTIVE_INDEX(index);
		}
	},
	beforeRouteEnter(to, from, next) {
		next(vm => {
			let path = vm.$route.fullPath;
			let isProject = path.startsWith('/home/project');
			if (isProject) {
				vm.SET_ACTIVE_INDEX('/home/project');
			} else {
				vm.SET_ACTIVE_INDEX(path);
			}
		});
	}
};
</script>

<style scoped lang="scss">
.textra {
	left: 5%;
	z-index: 100;
}
</style>
